<template>
  <div class="section">
    <div class="section-header">
      查询表格一
    </div>
    <div class="section-body">
      <div class="search">
        <div class="search-item">
          <span class="search-item__label">集合编号：</span>
          <zle-input class="search-item__input" v-model="param.code" placeholder="请输入集合编号" maxlength="55" clearable></zle-input>
        </div>
        <div class="search-item">
          <span class="search-item__label">集合名称：</span>
          <zle-input class="search-item__input" v-model="param.name" placeholder="请输入集合名称" maxlength="55" clearable></zle-input>
        </div>
        <div class="search-item">
          <span class="search-item__label">内容载体：</span>
          <zle-select class="search-item__input" v-model="param.content" placeholder="请选择内容载体" clearable>
            <zle-option v-for="(item, index) in listOptions" :key="index" :label="item.label" :value="item.value"></zle-option>
          </zle-select>
        </div>
        <div class="search-button">
          <zle-button type="primary" class="el-icon-search" @click="operateM('search')">查询</zle-button>
          <zle-button class="el-icon-refresh-left" @click="operateM('reset')">重置</zle-button>
        </div>
      </div>
      <div class="operate">
        <div class="operate-tab">
          <zle-tabs v-model="activeName" type="card" @tab-click="clickTab">
            <el-tab-pane label="用户管理" name="first"></el-tab-pane>
            <el-tab-pane label="配置管理" name="second"></el-tab-pane>
            <el-tab-pane label="角色管理" name="third"></el-tab-pane>
            <el-tab-pane label="定时任务补偿" name="fourth"></el-tab-pane>
          </zle-tabs>
        </div>
        <div class="operate-button">
          <span class="el-icon-plus button-item">新建</span>
          <span class="button-item">批量导入</span>
          <span class="el-icon-download button-item">下载</span>
          <span class="el-icon-refresh button-item">刷新</span>
          <zle-popover placement="left-start" width="160" trigger="hover">
            <div class="setting">
              <div class="setting-operate">
                <span class="setting-operate__all">
                  <zle-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAll"></zle-checkbox>
                  展示列
                </span>
                <span class="setting-operate__reset" @click="resetSetting">重置</span>
                <i class="el-icon-sort" @click="sort"></i>
              </div>
              <zle-checkbox-group v-model="checkList" @change="handleCheckedChange">
                <zle-checkbox v-for="item in settingList" class="setting-checkbox" :key="item" :label="item">
                  {{ item }}
                </zle-checkbox>
              </zle-checkbox-group>
            </div>
            <span slot="reference" class="el-icon-setting button-item">设置</span>
          </zle-popover>
        </div>
      </div>
      <div class="select-table">
        <zle-table class="table-box" :data="tableData" border hight="100%">
          <zle-table-column type="selection" width="55" align="center" />
          <zle-table-column type="index" label="序号" width="55" align="center" />
          <template v-for="(item, index) in tableList">
            <zle-table-column v-if="item.show" :key="index" :prop="item.prop" :label="item.label" min-width="180" align="center" />
          </template>
          <zle-table-column v-if="checkList.findIndex(item => item == '操作') != -1" type="index" label="操作" width="180" align="center">
            <template slot-scope="{ row }">
              <zle-button>编辑</zle-button>
            </template>
          </zle-table-column>
        </zle-table>
      </div>
      <div class="pagination">
        <zle-pagination :currentPage.sync="param.current" :pageSize.sync="param.size" @pagination="pageChange" :total="1000">
          >
        </zle-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'selectTableOne',
  data() {
    return {
      param: {
        current: 1,
        size: 20,
        code: '',
        name: '',
        content: '',
        activeName: ''
      },
      activeName: 'first',
      tableData: [
        {
          name: 'test1',
          content: 'test',
          way: 'testWay',
          num: '10',
          date: '2022-10-22'
        },
        {
          name: 'test2',
          content: 'test',
          way: 'testWay',
          num: '10',
          date: '2022-10-23'
        },
        {
          name: 'test2',
          content: 'test',
          way: 'testWay',
          num: '10',
          date: '2022-10-23'
        },
        {
          name: 'test2',
          content: 'test',
          way: 'testWay',
          num: '10',
          date: '2022-10-23'
        },
        {
          name: 'test2',
          content: 'test',
          way: 'testWay',
          num: '10',
          date: '2022-10-23'
        }
      ],
      listOptions: [
        {
          label: '选项一',
          value: '选项一'
        },
        {
          label: '选项二',
          value: '选项二'
        }
      ],
      checkList: ['集合名称', '内容载体', '筛选方式', '内容量', '创建时间', '操作'],
      settingList: ['集合名称', '内容载体', '筛选方式', '内容量', '创建时间', '操作'],
      tableList: [
        {
          label: '集合名称',
          prop: 'name',
          show: true
        },
        {
          label: '内容载体',
          prop: 'content',
          show: true
        },
        {
          label: '筛选方式',
          prop: 'way',
          show: true
        },
        {
          label: '内容量',
          prop: 'num',
          show: true
        },
        {
          label: '创建时间',
          prop: 'date',
          show: true
        }
      ],
      isIndeterminate: true,
      checkAll: false
    }
  },
  methods: {
    clickTab(val) {
      this.activeName = val.name
      this.param.activeName = val.name
      this.load()
    },
    load() {
      console.log(this.param)
    },
    operateM(type, item, index) {
      switch (type) {
        case 'search':
          this.param.current = 1
          this.load()
          break
        case 'reset':
          this.param = this.$options.data().param
          this.load()
          break
      }
    },
    sort() {
      this.settingList = this.settingList.reverse()
      if (this.settingList.findIndex(item => item == '操作') == 0) {
        this.settingList.shift()
        this.settingList.push('操作')
      }
      this.tableList = this.tableList.reverse()
    },
    resetSetting() {
      this.isIndeterminate = this.$options.data().isIndeterminate
      this.checkList = this.$options.data().checkList
      this.tableList = this.$options.data().tableList
    },
    pageChange() {
      this.load()
    },
    handleCheckAll(val) {
      this.checkList = val ? this.settingList : []
      this.isIndeterminate = false
      this.tableList.forEach(v => {
        v.show = this.checkList.length == 0 ? false : true
      })
    },
    handleCheckedChange(value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.settingList.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.settingList.length
      this.tableList.forEach(v => {
        v.show = this.checkList.findIndex(item => item == v.label) != -1
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.section {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  height: calc(100vh - 115px);
  overflow: auto;
  &-header {
    font-size: 1.4rem;
    font-weight: 600;
  }
  &-body {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    flex: 1;
    overflow: auto;
  }
}
.search {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  &-item {
    padding: 5px 0px;
    display: flex;
    align-items: center;
    margin-right: 10px;
    &__label {
      display: inline-block;
      font-size: 14px;
    }
    &__input {
      width: 200px;
      font-size: 14px;
    }
  }
}
.operate {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  background-color: rgb(247, 248, 250);
  padding: 0;
  box-sizing: border-box;
  .button-item {
    display: inline-block;
    padding: 0 15px;
    font-size: 14px;
    cursor: pointer;
  }
}
.select-table {
  flex: 1;
  overflow: auto;
}
.pagination {
  position: fixed;
  bottom: 15px;
  left: 220px;
  box-sizing: border-box;
  width: calc(100% - 220px);
  overflow: hidden;
  ::v-deep .el-pagination {
    display: flex;
    justify-content: flex-end;
  }
}
.setting {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  &__checkbox {
    margin-right: 15px;
  }
  &-operate {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
    &__reset {
      cursor: pointer;
      color: #409eff;
    }
    .el-icon-sort {
      position: absolute;
      right: 15px;
      bottom: -20px;
    }
  }
}
::v-deep .el-tabs__header {
  margin: 0;
}
